<template>
  <video
    width="100%"
    height="100%"
    src="https://interactive-examples.mdn.mozilla.net/media/examples/flower.webm"
    ref="videoElement"
    autoplay
    loop
    controls
  ></video>
</template>

<script>
import flvjs from "flv.js";

export default {
  name: "monitor",
  props: {
    data: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      flvPlayer: null,
    };
  },
  mounted() {
    let flvPlayer = flvjs.createPlayer({
      type: "flv",
      url: this.data,
    });
    flvPlayer.attachMediaElement(this.$refs.videoElement);
    flvPlayer.load();

    flvPlayer.on("LODING_COMPLETE", () => {
      this.$unlock();
      this.flvPlayer.play();
    });

    this.flvPlayer = flvPlayer;
  },
  methods: {},
};
</script>
<style lang="less" scoped>
video {
  object-fit: fill;
}
</style>
